import { observer } from "mobx-react-lite";
import Form from "../Form/Form";
import FormInput from "../FormInput/FormInput";
import { appStore } from "../../stores/AppStore";
import { InputStore } from "../../stores/InputStore";



const stores = {
  login: new InputStore(),
  password: new InputStore()
}

export const Admin = observer(() => {

  function onBtnClick() {
    if (appStore.isLoggedIn) {
      appStore.logout();
    } else {
      appStore.login(stores.login.value, stores.password.value);
    }
  }

  return (
    <main className="section">
      <h1 className="hidden"></h1>
      <Form btn={appStore.isLoggedIn ? '' : ''}
        onFormSubmit={onBtnClick}
        formElements={
          appStore.isLoggedIn ?
            <>
              <h2 className="text text_uppercase"> </h2>
              <FormInput disabled={true} value={appStore.user} text="" />
              <FormInput disabled={true} value={appStore.isAdmin ? '' : ''} text="" />
            </> :
            <>
              <FormInput {...stores.login} text=" " />
              <FormInput {...stores.password} type="password" text="" />
            </>
        }
      />
    </main>
  );
})