import { makeAutoObservable } from "mobx";

import api from "../utils/Api";

class AppStore {

  isLoggedIn = false; // 

  loading = false; // ,   / 

  user = ''; // 

  isAdmin = false; // 

  _countLoad = 0; // 

  constructor() {
    makeAutoObservable(this);
  }

  //  
  setLoading = (value) => {
    //       ,     
    if (value) {
      this._countLoad++;
      this.loading = value;
    } else
      this._countLoad--;
    this.loading = this._countLoad ? this.loading : value;
  }

  _setIsLoggedIn = (value) => {
    this.isLoggedIn = value;
  }

  _setUser = (value) => {
    this.user = value;
  }

  _setIsAdmin = (value) => {
    this.isAdmin = value;
  }

  // ,   
  setStates = ({ login, isAdmin, isLoggedIn }) => {
    this._setUser(login);
    this._setIsAdmin(isAdmin);
    this._setIsLoggedIn(isLoggedIn);
  }

  //         
  login = (login, password) => {
    this.setLoading(true);
    api.postLogin(login, password)
      .then((data) => {
        this.setStates({ ...data, isLoggedIn: true });
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        this.setLoading(false);
      })
  }

  //     
  logout = () => {
    this.setLoading(true);
    api.postLogout()
      .then((data) => {
        this._setIsLoggedIn(false);
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        this.setLoading(false);
      })
  }

  //       
  checkUser = () => {
    this.setLoading(true);
    api.getCheck()
      .then((data) => {
        if (!data.message)
          this.setStates({ ...data, isLoggedIn: true });
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        this.setLoading(false);
      })
  }
}

export const appStore = new AppStore();